import s from './order-details.module.css';
import checkMark from '../../img/check.svg';
import { useSelector } from 'react-redux';

const OrderDetails = () => {
  const { order } = useSelector(store => store.checkout);
  return (
    <div className={s.orderDetails}>
      <h1 className='text text_type_digits-large pb-8'>{order.number}</h1>
      <p className='text text_type_main-medium'> </p>
      <img className={s.accepted} src={checkMark} alt='order accepted' />
      <p className='text text_type_main-default pb-2'>   </p>
      <p className='text text_type_main-default text_color_inactive'>    </p>
    </div>
  )
}

export default OrderDetails;