import { Input, PasswordInput, Button } from "@ya.praktikum/react-developer-burger-ui-components";
import { useState } from "react";
import s from './page.module.css';
import { Link, Redirect, useLocation } from 'react-router-dom';
import { login } from "../services/actions/auth";
import { useDispatch, useSelector } from "react-redux";
import { useForm } from "../services/hooks/useForm";

export function LoginPage() {
  const dispatch = useDispatch();
  const location = useLocation();
  const { isAuth, loginRequest } = useSelector(store => store.auth);
  const [emailError, setEmailError] = useState(false);
  const [passError, setPassError] = useState(false);
  const path = location.state?.from.pathname;

  const {values, handleChange} = useForm({email: '', password: ''});

  const onSubmit = (e) => {
    e.preventDefault();
    if (!values.email.length || !values.password.length) {
      if (!values.email.length) {
        setEmailError(true);
      }
      if (!values.password.length) {
        setPassError(true)
      }
      return;
    }
    dispatch(login(values));
  }

  const onFocus = () => {
    setPassError(false);
    setEmailError(false);
  }

  if (isAuth) {
    return (
      <Redirect to={path ? path : '/'} />
    )
  }

  return (
    <div className={`${s.container} ${s.centered}`}>
      {Object.keys(values).length && <form className={s.content} onSubmit={(e) => onSubmit(e)}>
        <div className="text text_type_main-medium pb-6"></div>
        <div className={`${s.input} pb-6`}>
          <Input onChange={handleChange}
            value={values.email}
            name={'email'}
            placeholder={'E-mail'}
            type="email"
            error={emailError}
            errorText={"    "}
            onFocus={onFocus} />
        </div>
        <div className={`${s.input} pb-6`}>
          <PasswordInput onChange={handleChange}
            value={values.password}
            name={'password'}
            error={passError}
            errorText={"    "}
          />
          {/* {passError && <span className={`${s.error} text text_type_main-default`}>    </span>} */}
        </div>

        <Button type="primary"
          size="medium"
          disabled={loginRequest}>
          
        </Button>

        <p className="text text_type_main-default text_color_inactive pt-20 pb-4"> -  ?
          <Link to="/register" className={s.link}> </Link>
        </p>
        <p className="text text_type_main-default text_color_inactive"> ?
        <Link to="/forgot-password" className={s.link}>    </Link></p>
      </form>}
    </div>
  )
}